.header {
  position: absolute;
  z-index: 5;
  top: 0;
  left: 0;
  width: 100%;
  color: #ffffff;
  padding-top: 1.875rem;
  &__inner {
    display: flex;
    justify-content: space-between;
    gap: 6.25rem;
  }
  &__left {
    display: flex;
    align-items: center;
    gap: 1.25rem;
  }
  &__right {
    flex-grow: 1;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 3.5rem;
  }
  &__logo {
    width: 6.625rem;
    display: block;
    img {
      width: 100%;
    }
  }
  &__text {
    font-size: 0.875rem;
    line-height: 130%;
  }
  &__location {
    display: flex;
    align-items: center;
    gap: 0.875rem;
    &:hover {
      p {
        color: rgb(110, 185, 224);
      }
    }
    i {
      width: 1rem;
      flex-shrink: 0;
      img {
        width: 100%;
      }
    }
    p {
      font-size: 0.875rem !important;
      line-height: 130%;
      margin: 0 !important;
      transition: all 0.3s;
    }
  }
  &__wrap {
    display: flex;
    gap: 3.5rem;
  }
  &__email {
    display: flex;
    align-items: center;
    gap: 0.875rem;
    &:hover {
      p {
        color: rgb(110, 185, 224);
        border-color: rgb(110, 185, 224);
      }
    }
    i {
      width: 1rem;
      flex-shrink: 0;
      img {
        width: 100%;
      }
    }
    p {
      font-size: 0.875rem !important;
      line-height: 130%;
      color: rgb(115, 175, 39);
      border-bottom: 0.0625rem dashed rgb(115, 175, 39);
      margin: 0 !important;
      transition: all 0.3s;
    }
  }
  &__phones {
    display: flex;
    flex-direction: column;
  }
  &__phone {
    font-size: 1.125rem;
    line-height: 130%;
    &:hover {
      color: rgb(110, 185, 224);
    }
  }
  &__btn {
    background: transparent;
    margin-top: 0.625rem;
    margin-left: auto;
    &:hover {
      span {
        color: rgb(110, 185, 224);
        border-color: rgb(110, 185, 224);
      }
    }
    span {
      font-size: 0.875rem;
      line-height: 130%;
      color: rgb(115, 175, 39);
      border-bottom: 0.0625rem solid rgb(115, 175, 39);
      padding: 0;
      transition: all 0.3s;
    }
  }
}

@media screen and (max-width: 1399px) {
  .header {
    &__inner {
      gap: 2rem;
    }
    &__right {
      gap: 1.5rem;
    }
    &__wrap {
      gap: 1.5rem;
    }
  }
}

@media screen and (max-width: 1180px) {
  .header {
    &__wrap {
      flex-direction: column;
      gap: 0.625rem;
    }
    &__phone {
      text-align: right;
    }
  }
}

@media screen and (max-width: 1023px) {
  .header {
    &__wrap {
      flex-shrink: 0;
    }
    &__phone {
      font-size: 1rem;
    }
  }
}

@media screen and (max-width: 768px) {
  .header {
    padding-top: 1.25rem;
    &__inner {
      gap: 1.25rem;
    }
    &__right {
      gap: 1.25rem;
    }
    &__logo {
      width: 5.625rem;
    }
    &__text {
      font-size: 0.75rem;
    }
    &__location,
    &__email {
      p {
        font-size: 0.75rem !important;
      }
    }
    &__phone {
      font-size: 0.75rem;
    }
  }
}

@media screen and (max-width: 576px) {
  .header {
    &__inner {
      gap: 1rem;
    }
    &__right {
      gap: 1rem;
    }
    &__logo {
      width: 5rem;
    }
    &__text {
      display: none;
    }
  }
}

@media screen and (max-width: 399px) {
  .header {
    &__right {
      flex-direction: column;
      justify-content: flex-end;
      align-items: flex-end;
      gap: 0.625rem;
    }
    &__location {
      justify-content: flex-end;
      text-align: right;
      i {
        display: none;
      }
    }
  }
}