h2 {
  font-size: 2.25rem;
  line-height: 130%;
  font-weight: 600;
  color: #ffffff;
  margin: 0;
  .text_color {
    color: rgb(110, 185, 224);
  }
}
.btn-green {
  background: linear-gradient(180.00deg, rgb(115, 175, 39),rgb(60, 101, 26) 100%);
  background-color: rgb(115, 175, 39);
  border-radius: 3.125rem;
  &:hover {
    background: rgb(115, 175, 39);
  }
  &:active {
    background: linear-gradient(180.00deg, rgb(60, 101, 26) 100%);
  }
  span {
    font-size: 1.125rem;
    line-height: 130%;
    color: #ffffff;
    text-transform: uppercase;
    padding: 1.0625rem 2rem;
  }
}
@keyframes img {
  0% {
    transform: translate(0);
  }
  50% {
    transform: translate(0, -15%);
  }
  100% {
    transform: translate(0);
  }
}
.slick-arrow {
  position: static;
  width: 2.875rem;
  height: 2.875rem;
  background: linear-gradient(0.00deg, rgb(60, 101, 26),rgb(115, 175, 39) 100%);
  box-shadow: 0 0.625rem 0.9375rem -0.3125rem rgba(0, 0, 0, 0.3);
  svg {
    width: 1.5rem;
    height: auto;
  }
}
.button {
  display: flex;
  justify-content: center;
  align-items: center;
}
.navigation {
  display: inline-grid;
  grid-template-columns: 100%;
  gap: 1rem;
  padding: 3.125rem 0;
  li {
    font-size: 1.5rem;
    line-height: 130%;
    color: #000000;
    list-style: none;
    &:hover {
      color: rgb(110, 185, 224);
    }
  }
}
.left-anim {
  transform: translate(-60%, 0);
  opacity: 0;
  transition: all 0.6s;
}
.left-anim.active {
  transform: translate(0,0);
  opacity: 1;
}
.right-anim {
  transform: translate(60%, 0);
  opacity: 0;
  transition: all 0.6s;
}
.right-anim.active {
  transform: translate(0,0);
  opacity: 1;
}
.popup {
  background: transparent !important;
  padding: 0 !important;
}


@media screen and (max-width: 1399px) {
  h2 {
    font-size: 2rem;
  }
}

@media screen and (max-width: 1023px) {
  h2 {
    font-size: 1.75rem;
  }
  .btn-green {
    span {
      font-size: 1rem;
    }
  }
}

@media screen and (max-width: 576px) {
  h2 {
    font-size: 1.5rem;
  }
}