.footer {
  position: relative;
  color: #ffffff;
}
.footer-top {
  padding: 1rem 0 1.25rem;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 2rem;
  }
  &__left {
    display: flex;
    align-items: center;
    gap: 1.25rem;
  }
  &__right {
    display: flex;
    align-items: center;
    gap: 2rem;
  }
  &__logo {
    width: 6.625rem;
    display: block;
    img {
      width: 100%;
    }
  }
  &__text {
    font-size: 0.875rem;
    line-height: 130%;
  }
  &__socials {
    display: flex;
    align-items: center;
    gap: 0.5rem;
  }
  &__social {
    width: 2.5rem;
    height: 2.5rem;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 0.0625rem solid rgb(60, 101, 26);
    border-radius: 50%;
    svg {
      max-width: 1.5625rem;
    }
    &:hover {
      border-color: #3C651A;
      svg path {
        fill: #3C651A;
      }
    }
  }
  &__location {
    display: flex;
    align-items: center;
    gap: 0.875rem;
    &:hover {
      p {
        color: rgb(110, 185, 224);
      }
    }
    i {
      width: 1rem;
      flex-shrink: 0;
      img {
        width: 100%;
      }
    }
    p {
      font-size: 0.875rem !important;
      line-height: 130%;
      margin: 0 !important;
      transition: all 0.3s;
    }
  }
  &__email {
    display: flex;
    align-items: center;
    gap: 0.875rem;
    &:hover {
      p {
        color: rgb(110, 185, 224);
        border-color: rgb(110, 185, 224);
      }
    }
    i {
      width: 1rem;
      flex-shrink: 0;
      img {
        width: 100%;
      }
    }
    p {
      font-size: 0.875rem !important;
      line-height: 130%;
      color: rgb(115, 175, 39);
      border-bottom: 0.0625rem dashed rgb(115, 175, 39);
      margin: 0 !important;
      transition: all 0.3s;
    }
  }
  &__group {
    flex-shrink: 0;
  }
  &__phones {
    display: flex;
    flex-direction: column;
  }
  &__phone {
    font-size: 1.125rem;
    line-height: 130%;
    &:hover {
      color: rgb(110, 185, 224);
    }
  }
  &__btn {
    background: transparent;
    margin-top: 0.625rem;
    margin-left: auto;
    &:hover {
      span {
        color: rgb(110, 185, 224);
        border-color: rgb(110, 185, 224);
      }
    }
    span {
      font-size: 0.875rem;
      line-height: 130%;
      color: rgb(115, 175, 39);
      border-bottom: 0.0625rem solid rgb(115, 175, 39);
      padding: 0;
      transition: all 0.3s;
    }
  }
}
.footer-bottom {
  padding: 1.25rem 0 1.75rem;
  &__inner {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    gap: 1rem 2rem;
  }
  .b2b {
    width: 10.9375rem;
    img {
      width: 100%;
    }
  }
  &__text {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 0.3125rem;
    p {
      font-size: 0.875rem;
      line-height: 130%;
      margin: 0;
    }
    a:hover {
      color: rgb(110, 185, 224);
    }
  }
}

@media screen and (max-width: 1300px) {
  .footer-top {
    &__inner {
      flex-direction: column;
      align-items: start;
      gap: 1rem;
    }
    &__right {
      width: 100%;
      justify-content: flex-end;
    }
  }
}

@media screen and (max-width: 1023px) {
  .footer-top {
    &__phone {
      font-size: 1rem;
    }
  }
}

@media screen and (max-width: 768px) {
  .footer-top {
    &__inner {
      align-items: center;
      text-align: center;
    }
    &__left {
      flex-direction: column;
      gap: 1rem;
    }
    &__right {
      flex-direction: column;
      gap: 1rem;
    }
  }
  .footer-bottom {
    &__inner {
      flex-direction: column;
      align-items: center;
      text-align: center;
    }
    &__text {
      flex-direction: column;
      gap: 1rem;
    }
  }
}
