.section-group {
  position: relative;
  z-index: 2;
  background-image: url("../img/section-group1.webp");
  background-repeat: no-repeat;
  background-position: right;
  background-size: cover;
  &:before {
    content: '';
    position: absolute;
    z-index: 1;
    left: 0;
    bottom: -1rem;
    width: 100%;
    height: 60%;
    background: linear-gradient(0.00deg, rgb(0, 3, 12) 7.317%,rgba(0, 3, 12, 0) 94.389%);
    pointer-events: none;
  }
}
.main-section {
  padding: 15.625rem 0 5.625rem;
  .container-fluid {
    position: relative;
    z-index: 3;
  }
  &__inner {
    max-width: 53.75rem;
  }
  h1 {
    font-size: 3rem;
    line-height: 130%;
    font-weight: 600;
    color: rgb(110, 185, 224);
    margin: 0;
  }
  h2 {
    margin-top: 0.625rem;
  }
  ul {
    max-width: 33rem;
    display: inline-grid;
    grid-template-columns: 100%;
    gap: 2rem;
    margin-top: 2rem;
    li {
      position: relative;
      font-size: 1.25rem;
      line-height: 130%;
      color: #ffffff;
      padding-left: 1.75rem;
      &:after {
        content: '';
        position: absolute;
        top: 0.4375rem;
        left: 0;
        width: 0.75rem;
        height: 0.75rem;
        background: rgb(110, 185, 224);
        border-radius: 50%;
      }
      strong {
        font-weight: 700;
        color: rgb(110, 185, 224);
      }
    }
  }
  &__group {
    display: flex;
    align-items: center;
    gap: 1.5rem;
    margin-top: 5rem;
  }
  &__info {
    font-size: 0.875rem;
    line-height: 130%;
    color: #ffffff;
  }
  &__img {
    position: absolute;
    z-index: 2;
    top: 14.375rem;
    right: 22%;
    width: 22rem;
    img {
      width: 100%;
      animation: 8s linear infinite img;
    }
  }
  &__img2 {
    position: absolute;
    z-index: 2;
    left: 50%;
    bottom: -8.125rem;
    transform: translate(-50%, 0);
    width: 28.125rem;
    img {
      width: 100%;
    }
  }
  &__img3 {
    position: absolute;
    top: 6rem;
    right: 15rem;
    width: 12rem;
    display: none;
    img {
      width: 100%;
    }
  }
}
.services {
  z-index: 2;
  padding: 5.625rem 0 0 0;
  h2 {
    text-align: center;
  }
  &__cards {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 4.75rem / 3));
    gap: 1.5rem 2.375rem;
    margin-top: 2.5rem;
  }
}
.service-card {
  &:hover {
    .service-card__title {
      color: rgb(115, 175, 39);
    }
  }
  &__img {
    img {
      width: 100%;
    }
  }
  &__title {
    font-family: Tektur;
    font-size: 1.25rem;
    line-height: 130%;
    font-weight: 600;
    color: rgb(110, 185, 224);
    text-align: center;
    margin-top: 1rem;
  }
}
.section-group2 {
  position: relative;
  padding: 5.625rem 0 3.75rem;
  &:after {
    content: '';
    position: absolute;
    left: 0;
    bottom: -3.75rem;
    width: 100%;
    height: calc(100% + 44.6875rem);
    background-image: url("../img/section-group4.webp");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
  }
  .gradient {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    pointer-events: none;
    &:before {
      content: '';
      position: absolute;
      z-index: 1;
      top: -1rem;
      left: 0;
      transform: rotate(-180deg);
      width: 100%;
      height: 50%;
      background: linear-gradient(0.00deg, rgb(0, 3, 12) 7.317%,rgba(0, 3, 12, 0) 94.389%);
    }
    &:after {
      content: '';
      position: absolute;
      z-index: 1;
      left: 0;
      bottom: 0;
      width: 100%;
      height: 100%;
      background: linear-gradient(0.00deg, rgb(0, 3, 12) 7.317%,rgba(0, 3, 12, 0) 94.389%);
    }
  }
}
.works {
  z-index: 2;
  padding: 0 0 3.75rem 0;
  h2 {
    text-align: center;
  }
  &__slider {
    max-width: 50rem;
    margin: 2rem auto 0;
  }
  &__group {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3));
    text-align: center;
  }
  &__text {
    display: flex;
    align-items: flex-end;
    font-family: Tektur;
    font-size: 1.25rem;
    line-height: 130%;
    font-weight: 600;
    color: rgb(110, 185, 224);
    &:last-child {
      justify-content: flex-end;
    }
  }
  &__text2 {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    gap: 2rem;
    font-size: 1.125rem;
    line-height: 130%;
    color: #ffffff;
    margin-bottom: -0.625rem;
    i {
      width: 0.875rem;
      img {
        width: 100%;
      }
    }
  }
  &__slider-wrap {
    margin-top: 1.25rem;
  }
  .twentytwenty-handle {
    width: 2rem;
    height: 2rem;
    background: rgb(115, 175, 39);
    border-color: transparent;
    &:after,
    &:before {
      transform: translate(-50%, 0);
      width: 0.5rem;
      background: rgb(115, 175, 39);
      box-shadow: unset;
      margin-left: 0;
    }
    &:after {
      margin-top: 0;
    }
    &:before {
      margin-bottom:0;
    }
    span {
      display: none;
    }
  }
  &__slider-nav {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 1.25rem;
    margin-top: 2.5rem;
  }
  .news__dots-list {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    text-align: center;
    gap: 0.375rem;
    li {
      width: 0.5625rem;
      height: 0.5625rem;
      background: rgb(217, 217, 217);
      border-radius: 50%;
      color: transparent;
      cursor: pointer;
      &::selection {
        color: transparent;
      }
    }
    .slick-active {
      width: 2.4375rem;
      background: rgb(115, 175, 39);
      border-radius: 0.5625rem;

    }
  }
  &__img {
    position: absolute;
    top: 12rem;
    left: 9.375rem;
    width: 19rem;
    img {
      width: 100%;
    }
  }
  &__img2 {
    position: absolute;
    left: 5rem;
    bottom: 0;
    width: 11.6875rem;
    img {
      width: 100%;
    }
  }
  &__img3 {
    position: absolute;
    right: 5rem;
    bottom: 0;
    width: 24.9375rem;
    img {
      width: 100%;
    }
  }
}
.work-card {
  text-align: center;
  &__img {
    position: relative;
    height: auto !important;
    padding-bottom: 56%;
    img {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      width: 100%;
      height: 100%;
    }
  }
  &__title {
    font-family: Tektur;
    font-size: 1.25rem;
    line-height: 130%;
    font-weight: 600;
    color: #ffffff;
    margin-top: 2rem;
  }
}
.quiz {
  z-index: 2;
  padding: 3.75rem 0 0 0;
  &__inner {
    max-width: 59.75rem;
    display: flex;
    justify-content: center;
    align-items: center;
    background: rgba(12, 17, 32, 0.6);
    border: 0.0625rem solid rgb(110, 185, 224);
    box-shadow: 0 0 1.875rem 0 rgb(110, 185, 224),inset 0 0 1.875rem 0 rgb(110, 185, 224);
    backdrop-filter: blur(0.9375rem);
    margin: 0 auto;
  }
  &-head {
    width: 100%;
    height: 100%;
    min-height: 30.9375rem;
    display: flex;
    align-items: center;
    gap: 6.25rem;
    padding: 1.5rem 3.5rem;
  }
  &-head__img {
    width: 16.25rem;
    img {
      width: 100%;
    }
  }
  &-head__desc {
    max-width: 26.5625rem;
    p {
      font-size: 1.5rem;
      line-height: 130%;
      color: #ffffff;
      margin: 1rem 0 0 0;
      strong {
        font-weight: 700;
        color: rgb(115, 175, 39);
      }
    }
  }
  &-head__info {
    font-size: 1rem;
    line-height: 130%;
    color: #ffffff;
    margin-top: 2rem;
  }
  &-head__btn {
    margin-top: 1rem;
  }
  &-body {
    width: 100%;
    height: 100%;
    min-height: 30.9375rem;
    display: none;
    flex-direction: column;
    justify-content: space-between;
    gap: 2rem;
    padding: 3.75rem 3.5rem;
  }
  &-body--active {
    display: flex;
  }
  &-step {
    position: absolute;
    top: 4rem;
    left: 0;
    width: 100%;
    transform: translate(-2rem, 0);
    opacity: 0;
    pointer-events: none;
  }
  &-step--active {
    position: static;
    transform: translate(0, 0);
    opacity: 1;
    pointer-events: auto;
  }
  &__subtitle {
    font-family: Tektur;
    font-size: 1.25rem;
    line-height: 130%;
    font-weight: 600;
    color: rgb(110, 185, 224);
  }
  &__checkboxes {
    display: grid;
    grid-template-columns: repeat(4, calc(25% - 8.625rem / 4));
    gap: 2.875rem;
    margin-top: 1.25rem;
  }
  &__checkbox {
    position: relative;
    border: 0.0625rem solid rgb(110, 185, 224);
    padding: 0.75rem 0.75rem 1rem;
    input {
      position: absolute;
      z-index: 1;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      width: 100%;
      height: 100%;
      cursor: pointer;
      opacity: 0;
    }
    input:checked ~ label {
      span {
        background-image: url("../img/check.svg");
      }
    }
    &-img {
      display: flex;
      justify-content: center;
    }
    label {
      display: flex;
      justify-content: space-between;
      font-size: 0.875rem;
      line-height: 130%;
      color: #ffffff;
      margin-top: 1rem;
      span {
        width: 0.875rem;
        height: 0.875rem;
        flex-shrink: 0;
        background-repeat: no-repeat;
        background-position: center;
        background-size: auto;
        background-color: transparent;
        border: 0.0625rem solid #ffffff;
        border-radius: 0.3125rem;
        margin-top: 0.0625rem;
      }
    }
  }
  &__sizes {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    gap: 2rem;
    margin-top: 5rem;
  }
  &__size {
    display: flex;
    align-items: flex-end;
    gap: 0.875rem;
    label {
      font-size: 0.875rem;
      line-height: 130%;
      color: #ffffff;
    }
    input {
      width: 4.8125rem;
      height: auto;
      border: unset;
      background: transparent;
      border-bottom: 0.0625rem solid #ffffff;
      padding: 0 0.3125rem;
      font-size: 0.875rem;
      line-height: 130%;
      color: #ffffff;
    }
  }
  &__file {
    position: relative;
    display: inline-flex;
    gap: 1.5rem;
    margin-top: 1.125rem;
    input {
      position: absolute;
      z-index: 1;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      width: 100%;
      height: 100%;
      cursor: pointer;
      opacity: 0;
    }
    &-text {
      display: flex;
      align-items: center;
      gap: 0.5rem;
      font-size: 0.875rem;
      line-height: 130%;
      color: #ffffff;
    }
    &-name {
      font-size: 0.875rem;
      line-height: 130%;
      color: #ffffff;
    }
  }
  &__address {
    display: flex;
    align-items: flex-end;
    gap: 0.875rem;
    margin-top: 5rem;
    label {
      flex-shrink: 0;
      font-size: 0.875rem;
      line-height: 130%;
      color: #ffffff;
    }
    input {
      width: 100%;
      max-width: 28.75rem;
      height: auto;
      border: unset;
      background: transparent;
      border-bottom: 0.0625rem solid #ffffff;
      padding: 0 0.3125rem;
      font-size: 0.875rem;
      line-height: 130%;
      color: #ffffff;
    }
  }
  .quiz-error {
    font-size: 1rem;
    line-height: 130%;
    color: #ff0000;
    margin-top: 1rem;
    display: none;
  }
  .quiz-error--active {
    display: block;
  }
  &-nav {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 1.5rem / 2));
    align-items: flex-end;
    gap: 1.5rem;
  }
  .progress-bar {
    width: 100%;
    height: 0.875rem;
    background: rgba(115, 175, 39, 0.5);
    border-radius: 0.75rem;
    span {
      height: 100%;
      width: 0;
      display: block;
      background: rgb(115, 175, 39);
      border-radius: 0.75rem;
      transition: all 0.3s;
    }
  }
  .progress-text {
    font-size: 0.875rem;
    line-height: 130%;
    color: #ffffff;
    margin-top: 0.75rem;
  }
  &-btns {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &-prev {
    background: rgba(115, 175, 39, 0.5);
    pointer-events: none;
  }
  &-prev--active {
    background: linear-gradient(180.00deg, rgb(64, 106, 27),rgb(115, 175, 39) 100%);
    pointer-events: auto;
  }
  .end {
    display: none;
    .end-btn {
      margin-top: 2.5rem;
    }
  }
  .end--active {
    display: flex !important;
  }
}
