    line-height: 130%;
    font-weight: 700;
    color: rgb(110, 185, 224);
  }
  &__text {
    font-size: 1rem;
    line-height: 130%;
    color: #ffffff;
    margin-top: 0.75rem;
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp:9;
    -webkit-box-orient: vertical;
  }
  &__text--active {
    overflow: unset;
    display: block;
  }
  &__btn {
    display: inline-block;
    font-size: 0.875rem;
    line-height: 130%;
    color: rgb(115, 175, 39);
    border-bottom: 0.0625rem solid rgb(115, 175, 39);
    margin-top: 0.75rem;
    cursor: pointer;
    transition: all 0.3s;
    &:hover {
      border-color: transparent;
    }
    span:nth-child(2) {
      display: none;
    }
  }
  &__btn--active {
    span:nth-child(1) {
      display: none;
    }
    span:nth-child(2) {
      display: block;
    }
  }
}
